import React from 'react'
import { I18n } from 'react-i18nify-lite'
import { Group } from 'prey-stash'
import { Loading } from '../assets/Dialog'

const initMail = () => {
  if(window.Android){
    return window.Android.initMail();
  }else{
    return 'oso8@oso.cl'
  } 
}

class EmailSent extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      data: {},
      isLoading: false,
      isError: false,
      isOk: false,
      mail: initMail(),
    }
  }

  handleFocus = (e) => {
    this.setState({
      data: {},
      isError: false,
      isOk: false
    })
  }

  handleClick = (e)=>{
    this.setState({
      data: {} ,
      isLoading: true
      }, ()=>{setTimeout(this.callHandleClick, 0) 
    })
  }

  callHandleClick = (e)=>{
     var name=document.getElementById('email').value;
     if(window.Android){
       let json=window.Android.changemail(''+name);
       if(json!==''){
          var obj=JSON.parse(json);
          if (obj.hasOwnProperty("email")){
             this.setState({
                data: JSON.parse(json),
                isError: true
              });
          }
          if (obj.hasOwnProperty("ok")){
             this.setState({
                data: JSON.parse(json),
                isOk: true
              });
          }
          this.setState({
            isLoading: false
          })
       }else{
          this.setState({
            isLoading: false,
            mail: name
          })
       }
     }else{
        //let json='{"email":["no puede estar en blanco ","es demasiado corto (el mínimo es de 6 caracteres) ","debería ser un email "]}';
        let json='{"ok":["Si el correo ingresado es válido, recibirá las instrucciones de activación en breve."]}';
        if(json!==''){
            var obj2=JSON.parse(json);
            if (obj2.hasOwnProperty("email")){
               this.setState({
                  data: JSON.parse(json),
                  isError: true
                });
            }
            if (obj2.hasOwnProperty("ok")){
               this.setState({
                  data: JSON.parse(json),
                  isOk: true
                });
            }
            this.setState({
              isLoading: false
            });
        }else{
            this.setState({
              isLoading: false,
              mail: name
            });
        }
     } 
  }

  render() {
    return (
      <div className="fs-onboarding regular inverted">
      	<div className="column">
           <div className="sign-form">
           <h4 className="heading">{I18n.t(`email_validation.sent.title`)}</h4>
          <figure className="bg-fig">
            <i className="icon-email" />
          </figure>
          <h5>{I18n.t(`email_validation.sent.subtitle`)}</h5>
            
                <form autoComplete="off" className="form form-session" id="new-session"acceptCharset="UTF-8">
                       <ul className="form vvv">
                              <li>
                                <label>{I18n.t(`email_validation.sent.mail`)}</label>
                                <input id="email" name="email" required type="email" tabIndex="2" onFocus={this.handleFocus} defaultValue={this.state.mail}    />
                                {
                                        this.state.isError ? (
                                          <p ref='error' className="info info-error"><span>{this.state.data["email"]}</span></p>
                                        ) : null
                                }
                                {
                                        this.state.isOk ? (
                                          <p ref='error' className="info info-success"><span>{this.state.data["ok"]}</span></p>
                                        ) : null
                                }
                                
                              </li>
                            </ul>
                </form>
                <h5>{I18n.t(`email_validation.sent.foot_a`)}</h5>
                <h5>{I18n.t(`email_validation.sent.foot_b`)}</h5>
          </div>
        </div>
        <Group className="homeOptions">
          <Group>
            <a href="#" onClick={this.handleClick} className="btn fill success"><center><b>{I18n.t(`email_validation.sent.button`)}</b></center></a>
          </Group>
        </Group>
        {
                this.state.isLoading ? (
                  <Loading><code>{I18n.t(`dialog_login.loading`)}</code></Loading>
                ) : null
        }
        
      </div>
    )
  }
}

export default EmailSent
