import React from 'react'
import { Link } from 'react-router-dom'
import {Layout} from 'prey-stash'
import logomonoalt from '../images/branding/prey-logo-monotono-alt.svg'
import { I18n } from 'react-i18nify-lite'

import frontImg from '../assets/storage/front.jpg'
import backImg from '../assets/storage/back.jpg'

const getDatos = () => {
  if(window.Android){
    var datos=window.Android.getData()
    return JSON.parse(datos)
  } else {
    return JSON.parse("{ \"lat\": \"-33.42216110\",\"lng\": \"-70.61166382\",\"ssid\": \"Prey\",\"public_ip\": \"200.54.15.162\",\"imei\": \"352531088648686\",\"model\": \"Pixel Google\"}")
  }
}

class Report extends React.Component {

  componentDidMount () {
    try {
      var json=getDatos()
      document.getElementById('lat').innerHTML=json.lat
      document.getElementById('lng').innerHTML=json.lng
      document.getElementById('ssid').innerHTML=json.ssid
      document.getElementById('public_ip').innerHTML=json.public_ip
      document.getElementById('imei').innerHTML=json.imei
      document.getElementById('model').innerHTML=json.model
    }catch(error) {
    }
  }

  handleClickSecurity = (e)=>{
    if(window.Android){
      window.Android.security();
    } else {
      alert("ola oso security" )
    }
  }

  render() {
    return (
      <Layout scrollableFix={true} className="activation report-wrap fs-onboarding regular inverted">
        <div className="column reportdemo">
          <div className="bubble report demo">

            <div className="row report-logo">
              <img src={logomonoalt} alt="Prey" />
              <p>
                <small>{I18n.t("report.latitude")}</small>
                <br/>
                <b id="lat"></b>
              </p>
              <p>
                <small>{I18n.t("report.longitude")}</small>
                <br/>
                <b id="lng"></b>
              </p>
            </div>
            <div className="row">
              <span>
                <small>{I18n.t("report.ssid")}</small>
                <br/><b id="ssid"></b>
              </span>

              <span>
                <small>{I18n.t("report.public_ip")}</small>
                <br/><b id="public_ip"></b>
              </span>
            </div>
            <div className="row">
              <span>
                <small>{I18n.t("report.imei")}</small>
                <br/><b id="imei"></b>
              </span>
              <span>
                <small>{I18n.t("report.device_model")}</small>
                <br/><b id="model"></b>
              </span>
            </div>
            <div className="row">
              <div className="front">
              <br />
                {
                  window.Android ? (
                    <img src="file:///storage/emulated/0/Prey/back.jpg"   alt="" />
                  ): <img src={backImg}   alt="" />
                }
              </div>
              <div className="back">
              <br />
              {
                window.Android ? (
                  <img src="file:///storage/emulated/0/Prey/front.jpg" alt="" />
                ) : <img src={frontImg} alt="" />
              }

              </div>
            </div>
          </div>
        </div>
        <Link to="#" onClick={this.handleClickSecurity} className="btn fill success icon-right-open-big">
           {I18n.t("report.next")}
        </Link>
      </Layout>
    );
  }
}

export default Report;
