import React, { Component } from 'react'
import { I18n } from 'react-i18nify-lite'
import {Input, Layout, Group} from 'prey-stash'
import isomono from './images/branding/prey-iso-monotono-alt.svg'
import {Dialog, Loading} from './assets/Dialog'
import { Link } from 'react-router-dom'

class iOS extends Component {
  constructor(props) {
    super(props)
    this.state = {
      showDialog: false,
      isLoading: false
    }
  }
  handleLogin = (ev) => {
    ev.preventDefault()
    this.setState({
      showDialog: !this.state.showDialog
    })
  }
  handleSettings = (ev) => {
    ev.preventDefault()
    this.setState({
      showDialog: !this.state.showDialog
    })
  }
  handlePassword = (ev) => {
    ev.preventDefault()
  }

  render() {
    return (
      <Layout scrollableFix={true} className="activation home fs-onboarding regular inverted">
        <div className="container">
          <div className="column">
            <h2>
              <small>{I18n.t(`current_status`)}</small>
              <br/>
              <span>{I18n.t(`status.protected`)}</span>
            </h2>
            <figure className="anime uno">
              <figure className="anime dos">
                <figure className="anime tres">
                  <img src={isomono} alt="Prey" className="logo" />
                </figure>
              </figure>
            </figure>
          </div>
        </div>
        <Group className="homeOptions">
          <Group>
            <a onClick={this.handleLogin} href="#/" className="btn success icon-off">{I18n.t(`login`)}</a>
            <a onClick={this.handleSettings} href="#/" className="btn default icon-cog">{I18n.t(`settings`)}</a>
          </Group>
          <Group>
            <Link to="/whatsprey" className="btn default s icon-help-circle">
            {I18n.t(`whatsprey`)}
            </Link>
          </Group>
        </Group>
        {this.state.showDialog ? (
          <Dialog className="dialog">
            <div className="inner" key={'pin'}>
              <form autoComplete="off" className="form form-session" id="new-session" acceptCharset="UTF-8" >
                 <Input
                  label={I18n.t(`dialog_login.title`)}
                  className="groupForm"
                  id="passlogin"
                  placeholder={I18n.t(`dialog_login.holder`)}
                  checked="false"
                  type="password">&nbsp;</Input>
              </form>
              <Group>
                <Group>
                  <Link to="https://panel.preyproject.com/forgot" target="_blank" className="btn transparent default-text s icon-export">
                  {I18n.t(`dialog_login.forgot`)}
                  </Link>
                </Group>
                <Group>
                  {/* eslint-disable-next-line */}
                  <a onClick={this.handleLogin} href="#" className="btn default">{I18n.t(`dialog_login.cancel`)}</a>
                  {/* eslint-disable-next-line */}
                  <a onClick={this.handlePassword} href="#" className="btn">{I18n.t(`dialog_login.submit`)}</a>
                </Group>
              </Group>
            </div>
          </Dialog>
        ) : null }
        {/*
          // Loading with timeout
          <Loading className="loading" onLoad={this.loadingTimeout(2000)}><code>loading</code></Loading>
        */}
        {
          this.state.isLoading ? (
            <Loading className="loading"><code>loading</code></Loading>
          ) : null
        }
      </Layout>
    );
  }
}

export default iOS;
