import React from 'react'
import logomono from '../images/branding/prey-logo-monotono-alt.svg'
import { I18n } from 'react-i18nify-lite'
import { Link } from 'react-router-dom'


export default class Signup extends React.Component {

  constructor(props){
    super(props)
    this.state = {
      data: {}
    }
  }

  errorsFor = (name)=>{
    const errors = this.state.data[name]
    if(!errors) return
    if(errors.length === 0) return

    return <span ref='error' className="msg info-error">{
      errors.map((o)=>(
          <span>{o}</span>
        )
      )}
      </span>

  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }

  handleClick = (e)=>{
    var name=document.getElementById('name').value;
    var email=document.getElementById('email').value;
    var password1=document.getElementById('password1').value;
    var password2=document.getElementById('password2').value;
    var policy_rule_age=document.getElementById('user[policy_rule_age]').checked;
    var policy_rule_privacy_terms=document.getElementById('user[policy_rule_privacy_terms]').checked;

    if(window.Android){
      let json=window.Android.signup(''+name,''+email,''+password1,''+password2,''+policy_rule_age,''+policy_rule_privacy_terms);
      if(json!==''){
        this.setState(
          {data: JSON.parse(json)}
        )
      }else{
        this.props.history.push(`/${I18n._locale}/activation`);
      }
    } else {
      let json='{"password":["no puede estar en blanco"],"password_confirmation":["no coincide con la confirmación","no puede estar en blanco","es demasiado corto (el mínimo es de 6 caracteres)"],"policy_rule_age":["necesita confirmación"],"policy_rule_privacy_terms":["necesita confirmación"],"name":["no puede estar en blanco"],"email":["es demasiado corto (el mínimo es de 6 caracteres)","debería ser un email"]}';
      this.setState(
        {data: JSON.parse(json)}
      )
      //alert("ola oso signup name:"+name+" email:"+email+" password1:"+password1+" password2:"+password2+" policy_rule_age:"+policy_rule_age+"  policy_rule_privacy_terms:"+policy_rule_privacy_terms );;
    }
  }
  render() {
    return <div className="inner-content">
      <div className="content">
        <div className="login fs-onboarding regular inverted">
          <div className="column">
            <div className="sign-form">


              <form autoComplete="off" className="form form-session" id="new-session"acceptCharset="UTF-8">



                <h1>{I18n.t("signup.title")}</h1>
                <p className="lead">{I18n.t("signup.subtitle")}</p>
                <ul className="form vvv">
                  <li>
                    <label>{I18n.t("signup.field1")}</label>
                    <input id="name" name="name" required type="text" tabIndex="1" onFocus={this.handleFocus}  />
                    {this.errorsFor("name")}
                  </li>
                  <li>
                    <label>{I18n.t("signup.field2")}</label>
                    <input id="email" name="email" required type="email" tabIndex="2" onFocus={this.handleFocus} />
                    {this.errorsFor("email")}
                  </li>
                </ul>
                <ul className="form hvv">
                  <li>
                    <label className="tag-label" htmlFor="password">{I18n.t("signup.field3")}</label>
                    <input type="password" name="password1" id="password1" tabIndex="3" onFocus={this.handleFocus}/>
                    {this.errorsFor("password")}
                  </li>
                  <li>
                    <label className="tag-label" htmlFor="password">{I18n.t("signup.field4")}</label>
                    <input type="password" name="password2" id="password2" tabIndex="4" onFocus={this.handleFocus} />
                    {this.errorsFor("password_confirmation")}
                  </li>
                </ul>
                <ul className="form vvv">
                  <div className="checkbox">
                    <div className="custom-checkbox">
                        <input type="checkbox" id="user[policy_rule_age]" name="user[policy_rule_age]" value="on" />
                        <label htmlFor="user[policy_rule_age]" className="check-box"></label>
                    </div>
                    <label htmlFor="user[policy_rule_age]">{I18n.t("signup.check_old")}</label>
                  </div>
                  {this.errorsFor("policy_rule_age")}
                  <div className="checkbox">
                    <div className="custom-checkbox">
                        <input id="user[policy_rule_privacy_terms]" type="checkbox" name="user[policy_rule_privacy_terms]" value="on"  />
                        <label htmlFor="user[policy_rule_privacy_terms]" className="check-box"></label>
                    </div>
                    <label htmlFor="user[policy_rule_privacy_terms]">{I18n.t("signup.check_terms1")}  <a target="_blank" rel="noopener noreferrer" href="https://preyproject.com/terms">
                    {I18n.t("signup.button_terms")} </a> {I18n.t("signup.check_terms2")}  <a target="_blank" rel="noopener noreferrer" href="https://preyproject.com/privacy">{I18n.t("signup.button_privacy")} </a>.</label>
                  </div>
                  {this.errorsFor("policy_rule_privacy_terms")}
                </ul>
                <div className="button-group">
                  <Link to="#" value="Log in"  onClick={this.handleClick} className="cta btn xl success icon-enter">{I18n.t("signup.button_submit")}</Link>
                </div>
              </form>
              <p className="here">{I18n.t('signin.maybe')}</p>
              <Link to={`/${I18n._locale}/signin`} className="btn-link btn-block" >{I18n.t("signup.button_signin")}</Link>
              <a className="btn-link btn-block" href="https://panel.preyproject.com/forgot">{I18n.t("button_forgot")}</a>
            </div>
            <div id="footer">
              <footer className="copy">
                <div className="footer-links">
                  <a href="https://preyproject.com/terms" target="_blank" rel="noopener noreferrer">{I18n.t("button_terms")}</a>
                  <a href="https://preyproject.com/privacy" target="_blank" rel="noopener noreferrer">{I18n.t("button_privacy")}</a>
                  <a href="https://www.preyproject.com/privacy#cookies" target="_blank" rel="noopener noreferrer">{I18n.t("button_cookies")}</a>
                </div>
                  <img src={logomono} alt="Prey" className="logo" />
              </footer>
            </div>
          </div>
        </div>
      </div>
    </div>

  }

}
