import React from 'react'
import { Link } from 'react-router-dom'
import { I18n } from 'react-i18nify-lite'
import { Group} from 'prey-stash'

const handleClick = (e)=>{
  if(window.Android){
    window.Android.givePermissions()
  } else{
    alert("ola oso")
  }
}

const initAdminActive = () => {
  if(window.Android){
    return window.Android.initAdminActive()
  } else { 
    return false
  }
}
const initDrawOverlay = () => {
  if(window.Android){
    return window.Android.initDrawOverlay()
  } else { 
    return false
  }
}
const initCamera = () => {
  if(window.Android){
    return window.Android.initCamera()
  } else { 
    return false
  }
}
const initReadPhone = () => {
  if(window.Android){
    return window.Android.initReadPhone()
  } else { 
    return false
  }
}
const initWriteStorage = () => {
  if(window.Android){
    return window.Android.initWriteStorage()
  } else { 
    return false
  }
}
const initLocation = () => {
  if(window.Android){
    return window.Android.initLocation()
  } else { 
    return false
  }
}
const initBackgroundLocation = () => {
  if(window.Android){
    return window.Android.initBackgroundLocation()
  } else { 
    return false
  }
}
const initAndroid10OrAbove = () => {
  if(window.Android){
    return window.Android.initAndroid10OrAbove()
  } else { 
    return false
  }
}
const initAccessibility = () => {
  if(window.Android){
    return window.Android.initAccessibility()
  } else { 
    return false
  }
}


class Permission extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      hasAdminActive: initAdminActive(),
      hasDrawOverlay: initDrawOverlay(),
      hasLocation: initLocation(),
      hasCamera: initCamera(),
      hasReadPhone: initReadPhone(),
      hasWriteStorage: initWriteStorage(),
      hasBackgroundLocation: initBackgroundLocation(), 
      isAndroid10OrAbove: initAndroid10OrAbove(),
      hasAccessibility: initAccessibility(),
    }
  }

   


render() {
    return (
      <div className="perms fs-onboarding regular inverted">
      	<div className="column">
        	<h4 className="heading">{I18n.t("permissions.title")}</h4>
      		<figure className="bg-fig">
            <i className="icon-attention" />
          </figure>
          {
            !this.state.hasLocation&&!this.state.isAndroid10OrAbove ? (
                <p><h5>{I18n.t("permissions.location.head")}</h5>
                {I18n.t("permissions.location.body")}</p>
            ) :  null
          } 
          {
            !this.state.hasLocation&&this.state.isAndroid10OrAbove ? (
                <p><h5>{I18n.t("permissions.location.head")}</h5>
                {I18n.t("warning10.title")}</p>
            ) :  null
          }
          {
            this.state.hasLocation&&!this.state.hasBackgroundLocation&&this.state.isAndroid10OrAbove ? (
                <p><h5>{I18n.t("permissions.location.head")}</h5>
                {I18n.t("warning10.title")}</p>
            ) :  null
          }
           
                <p><h5>{I18n.t("permissions.camera.head")}</h5>{I18n.t("permissions.camera.body")}</p>
            
                <p><h5>{I18n.t("permissions.contact.head")}</h5>{I18n.t("permissions.contact.body")}</p>
             
                <p><h5>{I18n.t("permissions.storage.head")}</h5>{I18n.t("permissions.storage.body")}</p>
             
                <p><h5>{I18n.t("permissions.administrator_permission.head")}</h5>{I18n.t("permissions.administrator_permission.body")}</p>
             
                <p><h5>{I18n.t("permissions.over.head")}</h5>{I18n.t("permissions.over.body")}</p>
            
                <p><h5>{I18n.t("permissions.accessibility.head")}</h5>{I18n.t("permissions.accessibility.body")}</p>
            

     
        </div>
        <Group className="homeOptions">
          <Group>
            <Link to="#" onClick={handleClick} className="btn fill success">
               
              <span>{I18n.t("permissions.approve")}</span>
            </Link>
            
          </Group>
          {
            this.state.hasAdminActive&&
            this.state.hasDrawOverlay&&
            this.state.hasLocation&&
            this.state.hasCamera&&
            this.state.hasReadPhone&&
            this.state.hasWriteStorage&&
            this.state.isAndroid10OrAbove&&
            !this.state.hasBackgroundLocation ? (
              <Group>
                <Link to={`/${I18n._locale}/`}  className="btn fill">
                  <span>{I18n.t("permissions.next")}</span>
                </Link>  
              </Group>
            ) :  null
          }
        </Group>
      </div>
    )
  }
}

export default Permission
