import React, { Component } from 'react'
import { I18n } from 'react-i18nify-lite'
import { Input, Layout, Group } from 'prey-stash'
import isomono from './images/branding/prey-iso-monotono-alt.svg'
import {Dialog, Loading} from './assets/Dialog'
import { Link } from 'react-router-dom'

const initVerify = () => {
  if(window.Android){
    return window.Android.initVerify()
  } else {
    alert("ola oso initVerify" )
    return true
  }
}

class Android extends Component {
  constructor(props) {
    super(props)
    this.state = {
      showDialog: false,
      tipo: '',
      twoStep: false,
      data: {},
      isLoading: false,
      verify: initVerify(),
      showDialogPrivacy: false,
    }
  }

  errorsFor = (name) => {
    const errors = this.state.data[name]
    if(!errors) return
    if(errors.length === 0) return

    return <span className="msg info-error">{
      errors.map((o)=>(
          <span>{o}</span>
        )
      )}
      </span>
  }

  handleClose= (ev) => {
    this.setState({
      showDialog: false
    })
  }

  handleShowPrivacy = (ev) => {
    this.setState({ showDialogPrivacy: !this.state.showDialogPrivacy })
  }

  handleLogin = (ev) => {
    let isPowerOk=false;
    if(window.Android){
      isPowerOk=window.Android.isTimePasswordOk()
    }else{
      isPowerOk=false;
    }
    if(!isPowerOk){
      this.callHandleLogin()
    }else{
      if(window.Android){
        window.Android.openPanelWeb()
      } else {
        alert('open panel web');
      }
    }
  }
  callHandleLogin = (ev) => {
    this.setState({
        data: {},
    })
    if(window.Android){
      if(window.Android.getTwoStepEnabled()){
        this.setState({
          twoStep:true,
        })
      }else{
        this.setState({
          twoStep:false,
        })
      }
    }else{
      this.setState({
        twoStep:false,
      })
    }
    this.setState({
      tipo: 'login',
      showDialog: !this.state.showDialog
    })
  }

  handleSettings = (ev) => {
    let isPowerOk=false;
    if(window.Android){
      isPowerOk=window.Android.isTimePasswordOk()
    }else{
      isPowerOk=false;
    }
    if(!isPowerOk){
      this.callHandleSettings()
    }else{
      this.props.history.push(`/${I18n._locale}/security`);
    }
  }

  callHandleSettings = (ev) => {
    if(window.Android){
      if(window.Android.getTwoStepEnabled()){
        this.setState({
          twoStep:true,
        })
      }else{
        this.setState({
          twoStep:false,
        })
      }
    }else{
      this.setState({
        twoStep:true,
      })
    }
    this.setState({
      tipo:'setting',
      showDialog: !this.state.showDialog
    })

  }
  handlePassword = (ev) => {
    this.setState(
      {
        data: {} ,
        isLoading: true
      }, ()=>{setTimeout(this.callLogin, 0) }
    )
  }

  callLogin  = ()=>{
    var passlogin=document.getElementById('passlogin').value
    var passlogin2=""
    try{
      passlogin2=document.getElementById('passlogin2').value
    }catch(error) {}
    if(window.Android){
      let json=window.Android.login_tipo(''+passlogin,passlogin2,this.state.tipo);
      this.setState(
        {
          data: JSON.parse(json),
          isLoading: false
        },()=>{setTimeout(this.callExit,0,json) }
      )
    } else{
        //let json='{"error":["Credenciales inválidas"]}'
        let json='{"result":true}'
        setTimeout(()=>{
          this.setState(
            {
              data: JSON.parse(json),
              isLoading: false
            },()=>{setTimeout(this.callExit, 0,json)}
          )
        }, 5000)
    }
  }

  callExit  = (jsonSt)=>{

    let result=false
    try{
      var json=JSON.parse(jsonSt)
      if(jsonSt.includes("result"))
        result=json.result

      if(result){
        this.props.history.push(`/${I18n._locale}/security`);
      }
    }catch(err){
    }

  }
  handleForgot = (ev) => {
    if(window.Android){
      window.Android.forgot()
    } else{
      alert("ola forgot")
    }
  }

  handleFocus = (e) => {
    this.setState({data: {}})
  }

  loadingTimeout = (time) => {
    if (time) {
      clearTimeout(time);
    }
    time = setTimeout(() => {
      this.setState({
        isLoading: false
      })
    }, time);
  }



  render() {

    return (
      <Layout scrollableFix={true} className="activation home fs-onboarding regular inverted">
        <div className="container">
          <div className="column">
            <h2>
              <small>{I18n.t(`current_status`)}</small>
              <br/>
              <span>{I18n.t(`status.protected`)}</span>
            </h2>
            <figure className="anime uno">
              <figure className="anime dos">
                <figure className="anime tres">
                  <img src={isomono} alt="Prey" className="logo" />
                </figure>
              </figure>
            </figure>
          </div>
        </div>
        <Group className="homeOptions">
          <Group>
            {/* eslint-disable-next-line */}
            <a onClick={this.handleLogin} className="btn success icon-off">{I18n.t(`login`)}</a>
            {/* eslint-disable-next-line */}
            <a onClick={this.handleSettings} className="btn default icon-cog">{I18n.t(`settings`)}</a>
          </Group>
          <Group>
            <Link to={`/${I18n._locale}/whatsprey`} className="btn default s icon-help-circle">
              {I18n.t(`whatsprey`)}
            </Link>
            <Link to={`/${I18n._locale}/privacy`} className="btn default s icon-spy">
              {I18n.t(`your_privacy`)}
            </Link>
          </Group>
        </Group>
        {this.state.showDialogPrivacy&&(
          <Dialog
            className={`dialog privacy`}
            >
            <div className="inner" key={'pin'}>
              <h1 className="icon-spy" />
              <p>{I18n.t(`permissions.disclaimer.body`)}</p>
              <h4></h4>
              <ul>
                <li>The locations is only requested when needed</li>
                <li>You can customize your reports to include images or not</li>
                <li>We will <b>NEVER</b> share, distribute, nor sell your data</li>
                <li>Keep in mind, that some restrictions may apply depending on the current Android version</li>
              </ul>
              <p>Cheers, Stay safe!</p>
              <Group>
                <Group>
                  <a onClick={this.handleShowPrivacy} className="btn default">{I18n.t(`dialog_login.cancel`)}</a>
                  <a onClick={this.handlePassword} className="btn">{I18n.t(`dialog_login.submit`)}</a>
                </Group>
              </Group>
            </div>
          </Dialog>
        )}
        {this.state.showDialog ? (
          <Dialog
            className={`dialog`}
            >
            <div className="inner" key={'pin'}>
              <form autoComplete="off" className="form form-session" id="new-session" acceptCharset="UTF-8"  onFocus={this.handleFocus}>
                 <Input
                  label={I18n.t(`dialog_login.title`)}
                  className="groupForm"
                  id="passlogin"
                  placeholder={I18n.t(`dialog_login.holder`)}
                  checked={'false'}
                  type="password">&nbsp;</Input>
                  {this.state.twoStep ? (
                  <Input
                  label={I18n.t(`dialog_login.title2`)}
                  className="groupForm"
                  id="passlogin2"
                  checked={'false'}
                  placeholder={I18n.t(`dialog_login.holder2`)}
                  type="password">&nbsp;</Input>
                  ) : null }
              </form>
              <Group>
                <Group>
                {this.errorsFor("error")}
                </Group>
                <Group>
                  <Link onClick={this.handleForgot} to={`/${I18n._locale}/`} className="btn transparent default-text s icon-export">
                  {I18n.t(`dialog_login.forgot`)}
                  </Link>
                </Group>
                <Group>
                  {/* eslint-disable-next-line */}
                  <a onClick={this.handleClose} className="btn default">{I18n.t(`dialog_login.cancel`)}</a>
                  {/* eslint-disable-next-line */}
                  <a onClick={this.handlePassword} className="btn">{I18n.t(`dialog_login.submit`)}</a>
                </Group>
              </Group>
            </div>
          </Dialog>
        ) : null }
        {
          this.state.isLoading ? (
            <Loading><code>{I18n.t(`dialog_login.loading`)}</code></Loading>
          ) : null
        }
      </Layout>
    );
  }
}

export default Android;
