import React from 'react'
import { Link } from 'react-router-dom'
import { Input, Group } from 'prey-stash'
import { Dialog } from '../assets/Dialog'
import { I18n } from 'react-i18nify-lite'
import Select from 'react-select';

const initRunBackground = () => {
  if(window.Android){
    return window.Android.initBackground()
  } else {
    return false
  }
}

const initPin = () => {
  if(window.Android){
    return window.Android.initPin()
  } else {
    return false
  }
}

const initPin4 = () => {
  if(window.Android){
    return window.Android.initPin4()
  } else {
    return 1333
  }
}



const initUninstall = () => {
  if(window.Android){
    return window.Android.initUninstall()
  } else {
    return false
  }
}

const initShield = () => {
  if(window.Android){
    return window.Android.initShield()
  } else {
    return false
  }
}

// const getPin = () => {
//   if(window.Android){
//     return window.Android.getPin()
//   } else {
//     return "1111"
//   }
// }

const initVersion = () => {
  if(window.Android){
    return window.Android.initVersion()
  } else {
    return "2.0.8"
  }
}

const options = [
  { value: '0', label: ""},
  { value: '1', label: ""},
  { value: '2', label: ""},
  { value: '3', label: ""},
  { value: '4', label: ""},
  { value: '5', label: ""},
  { value: '6', label: ""}, 

];

const initScheduler = () => {
  options[0].label=I18n.t('scheduled.zero');
  options[1].label=I18n.t('scheduled.one');
  options[2].label=I18n.t('scheduled.two');
  options[3].label=I18n.t('scheduled.three');
  options[4].label=I18n.t('scheduled.four');
  options[5].label=I18n.t('scheduled.five');
  options[6].label=I18n.t('scheduled.six');
  if(window.Android){
    return options[window.Android.initScheduler()]
  } else {
    return options[0]
  }
}

let showDialogAndroid9 = false
let showDialog = false
let plisactive = initPin()

class Security extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      hasPin: initPin(),
      runBackground: initRunBackground(),
      runUninstall: initUninstall(),
      runShield: initShield(),
      pin4:initPin4(),
      version:initVersion(),
      scheduler:initScheduler(),
    }
  }

  componentDidMount () {
    this.setState({
      runBackground: this.changeRunBackground(),
      runUninstall: this.changeUninstall(),
      runShield: this.changeShield(),
    })
  }

  report() {
    if(window.Android){
      window.Android.report()
    } else {
      alert("ola oso report:" )
    }
  }

  wipe() {
    if(window.Android){
      window.Android.wipe()
    } else {
      alert("ola oso wipe:" )
    }
  }

  changeRunBackground = () => {
    var runnBg=document.getElementById('run-in-bg').checked
    this.setState({
      runBackground: runnBg
    })
    if(window.Android){
      window.Android.setBackground(runnBg)
    }
    return runnBg
  }

  changeUninstall = () => {
    this.setState({
      runUninstall: !this.state.runUninstall
    })
    var uninstall=document.getElementById('block-uninstall').checked
    if(window.Android){
      window.Android.setUninstall(uninstall)
    } else {
      //alert("ola oso setUninstall:"+uninstall )
    }
  }

  changeShield = () => {
    this.setState({
      runShield: !this.state.runShield
    })
  }

  changeShield2 = () => {
    this.setState({
      runShield: !this.state.runShield
    })
    var shield=document.getElementById('shield-off-btn').checked
    if(window.Android){
      if(window.Android.versionIsPieOrAbove()){
        showDialogAndroid9 =true
        document.getElementById('shield-off-btn').checked =false
        this.setState({
          runShield: false
        })
      } else {
        window.Android.setShieldOf(shield)
      }
    } else {
      showDialogAndroid9 =true
      document.getElementById('shield-off-btn').checked =false
        this.setState({
          runShield: false
        })
       //alert("ola oso shield:"+shield )
    }
  }

  handleModalOpen = (ev) => {
    showDialog =true
  }

  handleModalCancel = (ev) => {
    showDialog = false
  }

  
  handleModalCancel9 = (ev) => {
    showDialogAndroid9 = false
  }


  handleSavePin = () => {
    var pin_=document.getElementById('passlogin').value
    showDialog = true
    if(pin_!==''){
      this.setState({
        pin4: pin_
      })
      if(window.Android){
        window.Android.savepin2(pin_)
      } else {
        alert('ola oso save PIN:'+pin_)
      }
      plisactive = true
      showDialog =false
    }
  }

  handleDelete = () =>{
      if(window.Android){
        window.Android.savepin2('')
      } else {
        alert('ola oso handleDelete')
      }
      document.getElementById('shield-off-btn').checked =false
      document.getElementById('block-uninstall').checked =false
      this.setState({
        runUninstall: false
      })
      this.setState({
        runShield: false
      })
      plisactive = false
      showDialog =false

  }

  changeScheduler = selectedOption => {
    this.setState({
      scheduler: selectedOption
    })
    if(window.Android){
        window.Android.changeScheduler(""+selectedOption.value);
    } else {
        alert("changeScheduler:"+selectedOption.value);
    }
  }

  render() {
    return (
      <div className="security fs-onboarding regular inverted">
        <div className="column">
          <br/><br/>
          <h4 className="heading">{I18n.t("securitypage.title")}</h4>
          <p>{I18n.t("securitypage.subtitle")}</p>
          <figure className="bg-fig">
            <i className="icon-cog"/>
          </figure>
          <div className="item">
            <div className="row">
              <figure>
                <i className="icon-panel-settings"/>
              </figure>
              <h5>{I18n.t("securitypage.background_title")}</h5>
              <div className="toggle">
                <input type="checkbox" name="run-in-bg" className="toggle-checkbox" id="run-in-bg" onChange={this.changeRunBackground} checked={this.state.runBackground} />
                <label className="toggle-label" htmlFor="run-in-bg">
                    <span className="toggle-inner"></span>
                    <span className="toggle-switch"></span>
                </label>
              </div>
            </div>
            <p>{I18n.t("securitypage.background_subtitle")}</p>
          </div>
          <div className="item">
            <div className="row">
              <figure>
                <i className="icon-trash"/>
              </figure>
              <h5>{I18n.t("securitypage.wipe_title")}</h5>
              <Link to="#" onClick={this.wipe} className="btn">{I18n.t("securitypage.wipe_button")}</Link>
            </div>
            <span>{I18n.t("securitypage.wipe_subtitle")}</span>
          </div>
          <div className="line-through" />



          <div className="item">
            <div className="row">
              <figure>
                <i className="icon-panel-settings"/>
              </figure>
              <h5>{I18n.t("scheduled.title")}</h5>
              <Select id="scheduler_"  options={options}  onChange={this.changeScheduler} className="toggle-select"  value={this.state.scheduler}>
                  
              </Select>
            </div>
            <span>{I18n.t("scheduled.body")}</span>
          </div>

          <div className="line-through" />
          <div className="item">
            <div className="row">
              <figure>
                <i className="icon-login-box-lock"/>
              </figure>
              <h5>{I18n.t("securitypage.setup_pin_title")}</h5>
              <Link to="#" onClick={this.handleModalOpen}  className="btn">
              {plisactive ?  I18n.t("securitypage.setup_pin_activated"):I18n.t("securitypage.setup_pin_activate")}
              </Link>
            </div>
            <p>{I18n.t("securitypage.setup_pin_subtitle")}</p>
          </div>
          <div className={`prey-pin ${!plisactive ? 'inactive' : 'active' }`}>
            <i className="reference-icon icon-login-box-lock" />
            <div className="item">
              <div className="row">
                <figure>
                  <i className="icon-mobile-lock"/>
                </figure>
                <h5>{I18n.t("securitypage.uninstall_title")}</h5>
                <div className="toggle">
                  <input type="checkbox" name="block-uninstall" className="toggle-checkbox" id="block-uninstall"  onChange={this.changeUninstall} checked={this.state.runUninstall}/>
                  <label className="toggle-label" htmlFor="block-uninstall">
                      <span className="toggle-inner"></span>
                      <span className="toggle-switch"></span>
                  </label>
                </div>
              </div>
              <p>{I18n.t("securitypage.uninstall_subtitle")}</p>
            </div>
            <div className="item">
              <div className="row">
                <figure>
                  <i className="icon-lock-shield" />
                </figure>
                <h5>{I18n.t("securitypage.shield_title")}</h5>
                <div className="toggle">
                  <input type="checkbox" name="shield-off-btn" className="toggle-checkbox" id="shield-off-btn" onChange={this.changeShield2} checked={this.state.runShield} />
                  <label className="toggle-label" htmlFor="shield-off-btn">
                      <span className="toggle-inner"></span>
                      <span className="toggle-switch"></span>
                  </label>
                </div>
              </div>
              <p>{I18n.t("securitypage.shield_subtitle")}</p>
            </div>




          </div>
          <div className="line-through" />
            <div className="item">
              <div className="row">
                <figure>
                  <i className="icon-prey-camo"/>
                </figure>

                <h5>{I18n.t("securitypage.prey_for")}</h5>

              </div>
              <span>Version <small><code>{this.state.version}</code></small> - Prey Inc.</span>
          </div>
        </div>
        <Group className="homeOptions2">
            <Group>
              <Link to={`/${I18n._locale}/`} className="btn fill success">
                <i className="icon-ok" />
                {I18n.t("securitypage.home")}
              </Link>
            </Group>
          </Group>
        {showDialog  ?(
          <Dialog className="dialog">
            <div className="inner" key={'pin'}>
              <Input
                label={I18n.t("securitypage.dialog_4pin.title")}
                className="groupForm"
                id="passlogin"
                placeholder={plisactive ? this.state.pin4 : I18n.t("securitypage.dialog_4pin.holder")}
                type="number"
                >&nbsp;</Input>
              <Group>
                <Group>
                  <Link onClick={this.handleModalCancel} to="#" className="btn">{I18n.t("securitypage.dialog_4pin.cancel")}</Link>
                  <Link onClick={this.handleSavePin} to="#" className="btn">{I18n.t("securitypage.dialog_4pin.submit")}</Link>
                </Group>
              </Group>
              {plisactive ?(
              <Link to="#" onClick={this.handleDelete}  className="btn fill default error-text s icon-trash">{I18n.t("securitypage.dialog_4pin.remove_pin")}</Link>
              ):null}
            </div>
          </Dialog>
        ) : null }
        {showDialogAndroid9  ? (
          <Dialog className="dialog message">
            <div className="inner" key={'alert'}>
              <h3>{I18n.t("securitypage.not_android9_title")}</h3>
              <p>{I18n.t("securitypage.not_android9_subtitle")}</p>
              <Group>
              <Group>
                <Link onClick={this.handleModalCancel9} to="#" className="btn">{I18n.t("securitypage.not_android9_close")}</Link>
              </Group>
            </Group>
            </div>
          </Dialog>
        ) : null }


      </div>
    )
  }
}

export default Security
