import React from 'react'
import { I18n } from 'react-i18nify-lite'
import { Group } from 'prey-stash'
import { Dialog } from '../assets/Dialog'



class EmailSentOk extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      data: {},
      isEmailSent: true
    }
  }

  closeEmailSent = (e) => {
    e.preventDefault()
    this.setState({
      isEmailSent: false 
    })
    this.props.history.push(`/${I18n._locale}/activation`);
  }

  render() {
    return (
      <div className="fs-onboarding regular inverted">
           {
          this.state.isEmailSent ? (
            <Dialog
              className={`dialog`}
              >
              <div className="inner" key={'pin'}>
                <div className="content">
                  <h5>{I18n.t(`email_validation.ok.title`)}</h5>
                  <p>{I18n.t(`email_validation.ok.body`)}</p>
                </div>
                <Group>
                  <Group>
                    {/* eslint-disable-next-line */}
                    <a onClick={this.closeEmailSent} className="btn">{I18n.t(`email_validation.ok.button`)}</a>
                  </Group>
                </Group>
              </div>
            </Dialog>
          ): null
        } 
         
      </div>
      
    )
  }
}

export default EmailSentOk
